import React, { useState } from 'react'
import PropTypes from 'prop-types'
import SeriesView from './views/SeriesView/SeriesView'
import SeriesEdit from './views/SeriesEdit/SeriesEdit'
import Slideshow from './views/Slideshow/Slideshow'
import PhotoViewTool from './views/PhotoViewTool/PhotoViewTool'
import { SeriesConfig } from './contexts'
import NewPhoto from './NewPhoto'
import SeriesClass from './lib/Series'

const MODES = {
  SERIES_VIEW: 'series',
  SERIES_EDIT: 'editseries',
  NEWPHOTO: 'newphoto',
  NEWSERIES: 'newseries',
  REVIEW: 'review',
  PHOTO_VIEW: 'photoviewer',
  PHOTO_VIEW_NEW: 'newphotoviewer',
  SLIDESHOW: 'slideshow'
}
const VIEW_ONLY_MODES = [MODES.SERIES_VIEW, MODES.PHOTO_VIEW, MODES.SLIDESHOW]
const VALID_STARTMODES = [MODES.SERIES_VIEW, MODES.NEWPHOTO, MODES.NEWSERIES, MODES.PHOTO_REVIEW]

const RepeatablePhotos = ({
  modeName, series, inputMethod: inputMethodINIT, seriesConfig, allowEdit,
  onChange, onClose, onDelete, onNewSeries
}) => {
  // Manage view modes separately
  const [mode, _changeMode] = useState({
    current: {
      name: modeName || MODES.SERIES_VIEW,
      pid: null,
      inputMethod: inputMethodINIT
    },
    history: []
  })

  // console.log('STATE', { mode, series, seriesConfig })

  // When anything changes about a photo
  const onUpdatePhoto = (photo) => onChange({
    ...series,
    photos: series.photos.map(sp => sp.id === photo.id ? photo : sp)
  })

  const onCreatePhoto = (photo) => {
    onChange({
      ...series,
      photos: [...series.photos, photo]
    })
    changeMode(MODES.PHOTO_VIEW_NEW, photo.id)
  }

  // When a photo is removed
  const onDeletePhotos = (photoIdArr) => onChange({
    ...series,
    photos: series.photos.filter(sp => photoIdArr.indexOf(sp.id) < 0)
  })

  // Here's our modeChanger. My guess it will get a lot more complicated
  // once the reducers get in and working
  const changeMode = (newModeName, newPid, newInputMethod) => {
    if (mode === null) onClose()
    if (!allowEdit && VIEW_ONLY_MODES.indexOf(newModeName) < 0) {
      console.error('ILLEGAL MODE CHANGE!!!!!', { from: mode.current, to: newModeName, allowEdit })
    }
    else {
      // console.log('CHANGING MODE TO', newModeName)
      _changeMode({
        current: {
          name: newModeName,
          pid: newPid,
          inputMethod: newInputMethod
        },
        history: [...mode.history, mode.current]
      })
    }
  }
  const lastMode = () => {
    // console.log('REPEATABLEPHOTOS lastMode', mode)
    if (mode.history.length > 0) {
      const oldHistory = [...mode.history]
      const oldMode = oldHistory.pop()
      _changeMode({
        current: oldMode,
        history: oldHistory
      })
    }
    else {
      onClose()
    }
  }

  const onCreateSeries = (series) => {
    onNewSeries(series)
    onClose()
  }

  // The action to initiate the photo viewer
  const viewPhoto = (pid) => {
    // console.log('DEBUG', mode, series.photos.find(p => p.id === pid))
    changeMode(MODES.PHOTO_VIEW, pid)
  }

  let component = null

  switch (mode.current.name) {
    case MODES.SERIES_VIEW:
      component = (
        <SeriesView
          series={series}
          onMenuBack={onClose}
          allowEdit={allowEdit}
          onSlideshowStart={() => changeMode(MODES.SLIDESHOW)}
          onCameraAdd={() => changeMode(MODES.NEWPHOTO, null, 'camera')}
          onLibraryAdd={() => changeMode(MODES.NEWPHOTO, null, 'library')}
          onEditSeries={() => changeMode(MODES.SERIES_EDIT)}
          onChange={onChange}
          onDeletePhotos={onDeletePhotos}
          onDeleteSeries={onDelete}
          onTileClick={viewPhoto}
        />
      )
      break
    case MODES.SERIES_EDIT:
      component = (
        <SeriesEdit
          series={series}
          allowEdit={allowEdit}
          onMenuBack={() => changeMode(MODES.SERIES_VIEW)}
          onChange={onChange}
          onDeleteSeries={onDelete}
        />
      )
      break
    case MODES.PHOTO_VIEW:
      component = (
        <PhotoViewTool
          photo={series.photos.find(p => p.id === mode.current.pid)}
          allowEdit={allowEdit}
          onMenuBack={lastMode}
          onUpdatePhoto={onUpdatePhoto}
        />
      )
      break
    case MODES.PHOTO_VIEW_NEW:
      component = (
        <PhotoViewTool
          photo={series.photos.find(p => p.id === mode.current.pid)}
          allowEdit={allowEdit}
          onMenuBack={() => changeMode(MODES.SERIES_VIEW)}
          newPhoto
          onUpdatePhoto={onUpdatePhoto}
        />
      )
      break
    case MODES.SLIDESHOW:
      component = (
        <Slideshow photos={series.photos} onCancel={lastMode} />
      )
      break
    case MODES.NEWPHOTO:
      component = (
        <NewPhoto
          inputMethod={mode.current.inputMethod}
          photos={series ? series.photos : null}
          onCreate={onCreatePhoto}
          onCancel={lastMode}
        />
      )
      break
    case MODES.NEWSERIES:
      component = (
        <NewPhoto
          inputMethod={mode.current.inputMethod}
          isNewSeries
          onCreate={onCreateSeries}
          onCancel={lastMode}
        />
      )
      break
    default:
      console.log('REPEATABLEPHOTO: default state. you should NEVER see this', mode)
      component = null
  }

  return (
    <SeriesConfig.Provider value={seriesConfig}>
      {component}
    </SeriesConfig.Provider>
  )
}

RepeatablePhotos.propTypes = {
  modeName: PropTypes.oneOf(VALID_STARTMODES),
  seriesConfig: PropTypes.object.isRequired,
  inputMethod: PropTypes.oneOf(['camera', 'library']),
  series: PropTypes.object,
  allowEdit: PropTypes.bool,
  // Now some functions to handle actions we might take
  onChange: PropTypes.func.isRequired,
  onNewSeries: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}
RepeatablePhotos.defaultProps = {
  mode: MODES.SERIES_VIEW,
  canEdit: false
}

export default RepeatablePhotos
