import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  Chip
} from '@material-ui/core'
import {
  Cancel as CancelIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import config from '../config'
export const tagChips = {
  height: 20
}

const style = theme => ({
  root: {
    fontSize: '0.6em',
    height: tagChips.height,
    marginRight: theme.spacing.unit / 2,
    marginTop: theme.spacing.unit / 2
  },
  deleteIcon: {
    width: tagChips.height - 1,
    height: tagChips.height - 1
  },
  chipAvatar: {
    width: tagChips.height,
    height: tagChips.height
  },
  ...config.tagColors.reduce((acc, tagCol, idx) => ({
    ...acc,
    [`tag-${idx}`]: {
      background: `${tagCol}!important`,
      color: `${theme.palette.getContrastText(tagCol)}!important`
    }
  }), {})
})

const TagChip = ({ classes, edit, tag, group, onDelete, ...props }) => {
  return (
    <Chip
      label={tag.label}
      data={tag.id}
      onDelete={edit ? () => onDelete(tag) : null}
      deleteIcon={<CancelIcon className={classes.deleteIcon} />}
      className={classNames(classes.root, classes[`tag-${group}`])}
      {...props}
    />
  )
}

TagChip.propTypes = {
  classes: PropTypes.object.isRequired,
  edit: PropTypes.bool,
  tag: PropTypes.object.isRequired,
  onDelete: PropTypes.func,
  group: PropTypes.number.isRequired
}

export default withStyles(style)(TagChip)
