import React, { useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  IconButton,
  Typography
} from '@material-ui/core'
import {
  Edit as EditIcon,
  Close as CloseIcon,
  Check as CheckIcon
  // Refresh as RefreshIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'

const headerStyle = theme => ({
  root: {
    display: 'flex'
    // borderBottom: `1px solid ${theme.palette.grey['A200']}`
  },
  fieldTitle: {
    flexGrow: 1,
    display: 'block',
    color: theme.palette.text.secondary
  },
  iconButton: {
    padding: theme.spacing.unit * 0.75,
    '& svg': {
      fontSize: theme.spacing.unit * 2
    }
  },
  editButton: {
    color: theme.palette.grey['500']
  },
  cancelButton: {
    color: theme.palette.error.main,
    marginLeft: theme.spacing.unit
  }
})

const useFormFieldState = (defaultValue, allowEdit, onChangeExt, permaEdit) => {
  const [editing, setEditing] = useState(false)
  const [value, _onChange] = useState(defaultValue)

  const onChange = (val) => {
    if (permaEdit) onChangeExt(val)
    _onChange(val)
  }

  // Save drops out of edit mode and returns the saved state value
  // NOTE: THIS DOESN'T SET THE STATE. USE ONCHANGE FOR THAT.
  const onSave = () => {
    setEditing(false)
    onChangeExt(value)
  }
  // Reset just resets the form element to its previous state
  const onReset = () => {
    onChange(defaultValue)
    if (permaEdit) onChangeExt(defaultValue)
  }
  // Cancel resets the form AND returns to view mode
  const onCancel = () => {
    setEditing(false)
    onReset()
  }

  /**
   * HOC for the form control header
   */
  const Header = withStyles(headerStyle)(({ classes, children }) => (
    <div className={classes.root}>
      <Typography variant='overline' className={classes.fieldTitle}>
        {children}
      </Typography>
      {allowEdit && !editing && !permaEdit && (
        <IconButton
          className={classNames(classes.editButton, classes.iconButton)}
          onClick={() => setEditing(true)}
        >
          <EditIcon className={classes.icon} />
        </IconButton>
      )}
      {allowEdit && editing && !permaEdit && (
        <div>
          <IconButton
            className={classNames(classes.checkButton, classes.iconButton)}
            onClick={onSave}
          >
            <CheckIcon className={classes.icon} />
          </IconButton>
          <IconButton
            className={classNames(classes.cancelButton, classes.iconButton)}
            onClick={onCancel}
          >
            <CloseIcon className={classes.icon} />
          </IconButton>
        </div>
      )}
    </div>
  ))
  Header.PropTypes = {
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.node),
      PropTypes.node
    ]).isRequired
  }

  return { editing, value, onChange, onSave, onReset, onCancel, Header }
}

export default useFormFieldState
