import React, { useState, useContext } from 'react'
import { SeriesConfig } from '../../../contexts'
import XYTagChooser from './XYTagChooser'
import Tags from '../../../lib/Tags'

const useTags = (photo, newSize, onChange) => {
  const seriesConfig = useContext(SeriesConfig)

  // we hold the tags in state so that every little drag motion
  // doesn't cause an updated state until you let go
  const [isDragging, setIsDragging] = useState(false)
  const [newTagDialog, setNewTagDialog] = useState(null)

  const tagClass = new Tags(seriesConfig, photo.tags)

  const onNewTag = (coords, tagType) => ({ value, __isNew__: isNew }) => {
    let addTag = isNew
      ? tagClass.createNewXYTagObj(value, tagType.id, coords)
      : tagClass.createExistingXYTagObj(value, tagType.id, coords)

    setNewTagDialog(null)
    onChange({ ...photo, tags: [...photo.tags, addTag] })
  }

  const onTagDelete = (tag) => () => {
    const tags = photo.tags.filter(oldTag => oldTag.id !== tag.id)
    onChange({
      ...photo,
      tags
    })
  }

  // When the photo is tapped, send the x,y coords of that tap
  // to a dialog we will open
  const onAddTagTrigger = (e) => {
    setNewTagDialog({
      x: e.pageX - (window.innerWidth - newSize.newWidth) / 2 * newSize.multiplier,
      y: e.pageY - (window.innerHeight - newSize.newHeight) / 2 * newSize.multiplier
    })
  }

  // This gets used by the drag AND the stop
  const updateTagLocation = ({ x, y }, tag) => {
    const newTags = photo.tags.map(oldTag => {
      if (oldTag.id !== tag.id) return oldTag
      else {
        let newX = x / newSize.multiplier
        let newY = y / newSize.multiplier

        return {
          ...oldTag,
          coords: { x: newX, y: newY }
        }
      }
    })
    return newTags
  }

  const onTagDragStop = (coords, tag) => {
    const tags = updateTagLocation(coords, tag)
    setIsDragging(false)
    onChange({
      ...photo,
      tags
    })
  }

  const onTagDragOver = (coords, tag) => {
    updateTagLocation(coords, tag)
    setIsDragging(true)
  }
  const TagChooser = () => (
    <XYTagChooser
      open={!isDragging && Boolean(newTagDialog)}
      coords={newTagDialog}
      seriesConfig={seriesConfig}
      onNewTag={onNewTag}
      onClose={() => setNewTagDialog(null)}
    />
  )

  return {
    onNewTag,
    onTagDelete,
    onAddTagTrigger,
    onTagDragOver,
    onTagDragStop,
    TagChooser,
    getRelXYTags: tagClass.getRelXYTags
  }
}

export default useTags
