import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  MobileStepper,
  IconButton,
  GridListTileBar,
  Badge
} from '@material-ui/core'
import {
  Label as LabelIcon,
  RemoveRedEye,
  KeyboardArrowLeft,
  KeyboardArrowRight
} from '@material-ui/icons'
import SwipeableViews from 'react-swipeable-views'
import Photo from '../Photo/Photo'
import { formatDate } from '../../lib/datelib'
import { withStyles } from '@material-ui/core/styles'
import style from './SwipeShow.style'

const SwipeShow = ({ classes, theme, photos, onImageSelect }) => {
  const [activePhoto, setActivePhoto] = useState(0)
  const [showTags, setShowTags] = useState(false)
  const singular = photos.length < 2

  const sortedPhotos = [...photos]
  sortedPhotos.sort((a, b) => parseInt(b.date, 10) - parseInt(a.date, 10))

  const handleForward = () => setActivePhoto(activePhoto >= sortedPhotos.length ? 0 : activePhoto + 1)
  const handleBack = () => setActivePhoto(activePhoto <= 0 ? sortedPhotos.length - 1 : activePhoto - 1)

  return (
    <div className={classes.root}>
      <div className={classes.inner}>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activePhoto}
          onChangeIndex={setActivePhoto}
          enableMouseEvents
        >
          {sortedPhotos.map((photo, idx) => (
            <div className={classes.photoWrapper} key={`swipeshowpic-${idx}`}>
              <Photo photo={photo} className={classes.photo} showTags={showTags} />
            </div>
          ))}
        </SwipeableViews>
        <IconButton size='small' className={classes.manualBack} onClick={handleBack} color='inherit' disabled={activePhoto === 0}>
          {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
        </IconButton>
        <IconButton size='small' className={classes.manualForward} onClick={handleForward} color='inherit' disabled={activePhoto === photos.length - 1}>
          {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
        </IconButton>
        <GridListTileBar
          title={singular ? 'Photo' : `Photo (${activePhoto + 1}/${sortedPhotos.length})`}
          subtitle={sortedPhotos.length > 0 && (
            <span>{formatDate(sortedPhotos[activePhoto].date)}</span>
          )}
          actionIcon={
            <React.Fragment>
              <IconButton className={classes.tagButton} onClick={() => setShowTags(!showTags)}>
                <Badge color={showTags ? 'primary' : 'secondary'} badgeContent={sortedPhotos[activePhoto].tags.filter(tag => tag.coords).length}>
                  <LabelIcon />
                </Badge>
              </IconButton>
              <IconButton className={classes.tagButton} onClick={() => onImageSelect(sortedPhotos[activePhoto].id)}>
                <RemoveRedEye />
              </IconButton>
            </React.Fragment>
          }
        />
      </div>
      {!singular &&
      <MobileStepper
        steps={sortedPhotos.length}
        position='static'
        activeStep={activePhoto}
        classes={{
          root: classes.mobileStepper,
          dots: classes.dots
        }}
      />
      }
    </div>
  )
}

SwipeShow.propTypes = {
  // These two come from Material UI
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  // This is just the basic shape of the photo object
  photos: PropTypes.arrayOf(PropTypes.shape({
    img: PropTypes.string,
    tags: PropTypes.array,
    date: PropTypes.date
  })),
  transition: PropTypes.number,
  onImageSelect: PropTypes.func.isRequired
}

SwipeShow.defaultProps = {
  transition: 5000,
  isFullScreen: false,
  slides: []
}

export default withStyles(style, { withTheme: true })(SwipeShow)
