import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import uuid from 'uuid/v4'
import {
  Paper,
  TextField,
  Typography,
  MenuItem
} from '@material-ui/core'
import Select from 'react-select'
import CreatableSelect from 'react-select/lib/Creatable'
import { withStyles } from '@material-ui/core/styles'
import TagChip from '../TagChip'
import useFormFieldState from './useFormFieldState'
import style from './TagControl.style'

/**
 * Just to be clear: the state here is being stored as []
 */
const TagControl = ({ classes, defaultValue, allowEdit, onChange: onChangeExt, permaEdit, hint, tagConfig, group, className }) => {
  const {
    editing, value, onChange, Header
  } = useFormFieldState(defaultValue, allowEdit, onChangeExt, permaEdit)

  const onSelectChange = (inputValue, { action, removedValue, option, ...other }) => {
    // If this was the last tag then close the menu
    switch (action) {
      case 'clear':
        onChange([])
        break
      case 'remove-value':
        // Remember we need to potentially remove it from the old AND the new
        onChange(value.filter(val => val.id !== removedValue.value))
        break
      case 'select-option':
        const newVal = {
          ...tagConfig.values.find(tcv => tcv.id === option.value),
          typeId: tagConfig.id
        }
        onChange([...value, newVal])
        break
      case 'create-option':
        const newLabel = inputValue.find(v => v.__isNew__).value
        const newCreateVal = {
          label: newLabel,
          id: uuid(),
          typeId: tagConfig.id
        }
        onChange([...value, newCreateVal])
        break
      default:
        break
    }
  }

  const SingleValue = ({ removeProps, data }) => (
    <TagChip tag={data} onDelete={removeProps.onClick} group={group} edit />
  )
  SingleValue.propTypes = {
    removeProps: PropTypes.any,
    data: PropTypes.any
  }

  const MultiValue = ({ removeProps, data }) => (
    <TagChip tag={data} onDelete={removeProps.onClick} group={group} edit />
  )
  MultiValue.propTypes = {
    removeProps: PropTypes.any,
    data: PropTypes.any
  }

  // Filter to just this tag type
  const tagValues = value.filter(tag => tag.typeId === tagConfig.id)
  // Now format it as {label, value: id} for react-select
  const selectOptions = tagValues.map(val => ({ label: val.label, value: val.id }))

  // Filter out things we haven't chosen yet
  // Also transform the options into a label/value pair for react-select
  const remainingTags = tagConfig.values
    .filter(val => tagValues.map(t => t.id).indexOf(val.id) === -1)
    .map(val => ({ label: val.label, value: val.id }))

  // console.log('TAG DEBUG', { value, tagValues, tagConfig })

  return (
    <div className={classNames(classes.root, editing && classes.rootEdit, className)}>
      <Header>{tagConfig.label}</Header>
      {!(editing || permaEdit)
        ? <div className={classes.tagContainer}>
          {tagValues.map((tag, idx) => (
            <TagChip tag={tag} key={`tagchip${idx}`} group={group} edit={editing} />
          ))}
        </div>
        : null
      }
      { (editing || permaEdit) && !tagConfig.allowAdditions && (
        <Select
          options={remainingTags}
          classes={classes}
          components={{
            Control,
            Menu,
            MultiValue,
            NoOptionsMessage,
            Option,
            Placeholder,
            SingleValue,
            ValueContainer
          }}
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      ) }
      { (editing || permaEdit) && tagConfig.allowAdditions && (
        <CreatableSelect
          options={remainingTags}
          classes={classes}
          components={{
            Control,
            Menu,
            MultiValue,
            NoOptionsMessage,
            Option,
            Placeholder,
            SingleValue,
            ValueContainer
          }}
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      )}
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>
  )
}

TagControl.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  hint: PropTypes.string,
  defaultValue: PropTypes.array,
  allowEdit: PropTypes.bool,
  permaEdit: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  tagConfig: PropTypes.object,
  group: PropTypes.number
}

export default withStyles(style)(TagControl)

function Control (props) {
  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: props.selectProps.classes.input,
          inputRef: props.innerRef,
          children: props.children,
          ...props.innerProps
        }
      }}
      {...props.selectProps.textFieldProps}
    />
  )
}

const NoOptionsMessage = ({ innerProps, selectProps, children }) => (
  <Typography
    color='textSecondary'
    className={selectProps.classes.noOptionsMessage}
    {...innerProps}
  >
    {children}
  </Typography>
)

const Menu = ({ selectProps, innerProps, children }) => (
  <Paper square className={selectProps.classes.paper} {...innerProps}>
    {children}
  </Paper>
)

const inputComponent = ({ inputRef, ...props }) => <div ref={inputRef} {...props} />

const Placeholder = (props) => (
  <Typography
    color='textSecondary'
    className={props.selectProps.classes.placeholder}
    {...props.innerProps}
  >
    {props.children}
  </Typography>
)

const Option = ({ innerRef, isFocused, isSelected, innerProps, children }) => (
  <MenuItem
    buttonRef={innerRef}
    selected={isFocused}
    component='div'
    style={{ fontWeight: isSelected ? 500 : 400 }}
    {...innerProps}
  >
    {children}
  </MenuItem>
)

const ValueContainer = ({ selectProps, children }) => (
  <div className={selectProps.classes.valueContainer}>{children}</div>
)
ValueContainer.propTypes = {
  selectProps: PropTypes.any,
  children: PropTypes.any
}
