import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import {
  CssBaseline,
  Dialog,
  DialogTitle,
  Typography,
  DialogContent,
  DialogActions,
  Button
} from '@material-ui/core'
// import { Menu as MenuIcon } from '@material-ui/icons'
import TagControl from '../controls/TagControl'
import TextAreaControl from '../controls/TextAreaControl'
import TextFieldControl from '../controls/TextFieldControl'
import DateControl from '../controls/DateControl'
import TagClass from '../../lib/Tags'
import Slider from '@material-ui/lab/Slider'
import MapDot from '../../components/maps/MapDot/MapDot'

import { withStyles } from '@material-ui/core/styles'
import style from './NewPhotoInfo.style'
import { SeriesConfig } from '../../contexts'

const NewPhotoInfo = ({ classes, photo, series, onChange, isNewSeries, onCancel, onSave }) => {
  const seriesConfig = useContext(SeriesConfig)
  const [rotation, setRotation] = useState(series && series.direction ? series.direction : -1)

  console.log('NewPhotoInfo', { photo, series, onChange, isNewSeries, onCancel, onSave })

  const onRotationChange = (dir) => {
    const newDir = dir >= 0 ? dir : null
    onChangeLocalSeries('direction', newDir)
    setRotation(newDir)
  }

  const onMapChange = ({ latitude, longitude }) => {
    onChangeLocalSeries('location', [latitude, longitude])
  }

  const onChangeLocal = (fieldName, val) => {
    console.log('onChangeLocal', fieldName, val)
    if (isNewSeries) {
      onChange({
        ...series,
        photos: [{ ...photo, [fieldName]: val }]
      })
    }
    else onChange({ ...photo, [fieldName]: val })
  }
  const onChangeLocalSeries = (fieldName, val) => {
    console.log('onChangeLocalSeries', fieldName, val)
    onChange({ ...series, [fieldName]: val })
  }

  const tagInst = new TagClass(seriesConfig, photo.tags)

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <Dialog
        fullScreen
        fullWidth
        scroll='paper'
        disableBackdropClick
        disableEscapeKeyDown
        onClose={onCancel}
        aria-labelledby='customized-dialog-title'
        open
      >

        <DialogTitle disableTypography className={classes.dlgTitleContainer}>
          <Typography variant='title' className={classes.dlgTitle}>
          New Photo Properties
          </Typography>
        </DialogTitle>
        <DialogContent>

          {isNewSeries && (
            <div className={classes.series}>
              <form className={classes.form} elevation={1} noValidate autoComplete='off'>
                <TextFieldControl
                  className={classes.field}
                  label='Series Name'
                  placeholder='New Series'
                  permaEdit
                  allowEdit
                  hint='give this series a name'
                  value={series.name}
                  onChange={val => onChangeLocalSeries('name', val)}
                />
                <TextAreaControl
                  className={classes.field}
                  label='Notes'
                  placeholder='Any instructions for the next person to add a picture to this series?'
                  multiline
                  rows='1'
                  rowsMax='4'
                  permaEdit
                  allowEdit
                  value={series.note}
                  onChange={val => onChangeLocalSeries('note', val)}
                />

                <div className={classes.mapWrapper}>
                  <Typography variant='overline' className={classes.formLabel}>Location</Typography>
                  <div style={{ height: 200, marginBottom: 10 }}>
                    <MapDot
                      editable
                      className={classes.map}
                      direction={rotation > 0 ? rotation : null}
                      latitude={series.location[0]}
                      longitude={series.location[1]}
                      onChange={onMapChange}
                    />
                  </div>
                  <Typography variant='overline' className={classes.mapFormLabel}>Direction {rotation > 0 ? '' : '(off)'}</Typography>
                  <Slider
                    className={classes.field}
                    classes={{ container: classes.slider }}
                    value={rotation || -1}
                    min={-1}
                    max={360}
                    step={1}
                    onChange={(e, value) => onRotationChange(value)}
                  />
                </div>
              </form>
            </div>
          )}

          <DateControl
            className={classes.field}
            label='Photo taken on'
            defaultValue={photo.date || null}
            onChange={val => onChangeLocal('date', val)}
            hint='Edit the date you took this picture'
            allowEdit
            permaEdit
          />

          <TextAreaControl
            label='Photo Note'
            className={classes.field}
            defaultValue={photo.note}
            placeholder='Anything special about this photo?....'
            onChange={val => onChangeLocal('note', val)}
            rows={1}
            rowsMax={5}
            allowEdit
            permaEdit
          />

          <Typography variant='subtitle1'>Category tags</Typography>

          {tagInst.seriesConfig.getNonXYTagTypes().map((tt, idx) => (
            <TagControl
              className={classes.field}
              defaultValue={photo.tags}
              tagConfig={tt}
              group={tagInst.seriesConfig.getTagTypeIndex(tt.id)}
              allowEdit
              permaEdit
              onChange={val => onChangeLocal('tags', val)}
              key={`tagtype-${idx}`}
            />
          ))}

        </DialogContent>

        <DialogActions>
          <Button onClick={onCancel} color='primary'>
  Cancel
          </Button>
          <Button onClick={onSave} color='primary'>
  Next
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

NewPhotoInfo.propTypes = {
  classes: PropTypes.object.isRequired,
  photo: PropTypes.object.isRequired,
  series: PropTypes.object,
  isNewSeries: PropTypes.bool,
  // onChange serves double duty here. I'm not sure if that's a good idea
  onChange: PropTypes.func.isRequired,
  // Standard functions
  onCancel: PropTypes.func.isRequired,
  onSave: PropTypes.func
}

export default withStyles(style)(NewPhotoInfo)
