import { openDb } from 'idb'
import seriesList from './seriesList.json'

const STORENAME = 'serieslist'
const dbPromise = openDb('serieslistDB', 1, upgradeDB => {
  console.log('UPGRADE NEEDED', upgradeDB)
  const store = upgradeDB.createObjectStore(STORENAME)
  console.log('UPGRADE NEEDED STORE', store)
  seriesList.forEach(async series => {
    await store.put(series, series.id)
  })
}).catch(e => {
  console.error('ERROR', e)
})

export default {
  async reload () {
    const db = await dbPromise
    const tx = db.transaction(STORENAME, 'readwrite')

    seriesList.forEach(async series => {
      await tx.objectStore(STORENAME).put(series, series.id)
    })
  },
  async get (key) {
    const db = await dbPromise
    return db.transaction(STORENAME).objectStore(STORENAME).get(key)
  },
  async getAll () {
    const db = await dbPromise
    return db.transaction(STORENAME).objectStore(STORENAME).getAll()
  },
  async set (key, val) {
    const db = await dbPromise
    const tx = db.transaction(STORENAME, 'readwrite')
    tx.objectStore(STORENAME).put(val, key)
    return tx.complete
  },
  async delete (key) {
    const db = await dbPromise
    const tx = db.transaction(STORENAME, 'readwrite')
    tx.objectStore(STORENAME).delete(key)
    return tx.complete
  },
  async clear () {
    const db = await dbPromise
    const tx = db.transaction(STORENAME, 'readwrite')
    tx.objectStore(STORENAME).clear()
    return tx.complete
  },
  async keys () {
    const db = await dbPromise
    return db.transaction(STORENAME).objectStore(STORENAME).getAllKeys()
  }
}
