import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  Typography,
  TextField
} from '@material-ui/core'
import useFormFieldState from './useFormFieldState'
import ExpandNote from './ExpandNote'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
  },
  rootEdit: {
  },
  textBody: {
    whiteSpace: 'pre-line',
    padding: theme.spacing.unit
  },
  hint: {
    // marginTop: theme.spacing.unit,
    // marginBottom: theme.spacing.unit
  },
  textField: {
    color: theme.palette.grey['900'],
    background: theme.palette.grey['200'],
    '& textarea': {
      color: theme.palette.grey['900'],
      background: theme.palette.grey['200']
      // padding: theme.spacing.unit
    }
  }
})

const TextAreaControl = ({ classes, defaultValue, permaEdit,
  collapsed, className,
  hint,
  collapsedHeight, label,
  allowEdit,
  onChange: onChangeExt, ...props }) => {
  const { editing, value, onChange, Header } = useFormFieldState(defaultValue, allowEdit, onChangeExt, permaEdit)

  const textRender = collapsed
    ? <ExpandNote text={value} height={collapsedHeight} />
    : <Typography variant='body2'>{value}</Typography>

  return (
    <div className={classNames(classes.root, editing && classes.rootEdit, className)}>
      <Header>{label}</Header>
      {!editing && !permaEdit
        ? <div className={classes.textBody}>{textRender}</div>
        : <TextField
          multiline
          fullWidth
          rows={5}
          rowsMax={10}
          value={value || ''}
          className={classes.textField}
          {...props}
          onChange={(e) => onChange(e.target.value)}
        />
      }
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>

  )
}

TextAreaControl.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  label: PropTypes.string.isRequired,
  defaultValue: PropTypes.string,
  hint: PropTypes.string,
  collapsed: PropTypes.bool,
  collapsedHeight: PropTypes.number,
  allowEdit: PropTypes.bool,
  permaEdit: PropTypes.bool,
  // These come in from the HOC
  onChange: PropTypes.func.isRequired
}

TextAreaControl.defaultProps = {
  collapsed: false,
  allowEdit: false
}

export default withStyles(style)(TextAreaControl)
