import React, { useState, useEffect, useRef } from 'react'
import ReactMapGL, { NavigationControl } from 'react-map-gl'
import PropTypes from 'prop-types'
// import WebMercatorViewport from 'viewport-mercator-project'
import useComponentSize from '../../lib/useComponentSize'
import MapStyles from './styles'
import StyleButtons from './styles/StyleButtons'
import config from '../../config'
import classNames from 'classnames'
import 'mapbox-gl/dist/mapbox-gl.css'
import './BaseMap.css'

/**
 * Note that this component technically has state but it is designed to be functionally stateless
 */
const BaseMap = (props) => {
  const [viewport, currStyle, setCurrStyle, onViewportChange] = useMap(props)
  const mapRef = useRef(null)
  const size = useComponentSize(mapRef)
  const {
    children, mapStyles,
    onMapDataEvent,
    stylechooser
  } = props

  return (
    <div className={classNames('BaseMap', props.className)} ref={mapRef} style={{ height: '100%', width: '100%' }}>
      <ReactMapGL
        {...viewport}
        height={size.height}
        width={size.width}
        mapStyle={mapStyles[currStyle].style}
        onData={onMapDataEvent}
        onViewportChange={viewport => onViewportChange({ ...viewport, ...size })}
        mapboxApiAccessToken={config.mapBoxToken} >
        { stylechooser ? <StyleButtons currStyle={currStyle} mapStyles={mapStyles} setCurrStyle={setCurrStyle} /> : null }
        {/* Augment children with some props they need to survive in this harsh world */}
        { !props.hideNavControls && <NavigationControl onViewportChange={viewport => onViewportChange({ ...viewport, ...size })} showZoom={!props.lockZoom} showCompass={!props.lockRotation} /> }
        {
          children && Array.isArray(children)
            ? children.map(c => handleChild(c, viewport, size.height, size.width))
            : handleChild(children, viewport, size.height, size.width)
        }
      </ReactMapGL>
    </div>
  )
}

function handleChild (child, viewport, height, width) {
  return typeof child === 'function'
    ? child(viewport, height, width)
    : child
}

BaseMap.propTypes = {
  // Anything else we want to add to the map
  children: PropTypes.any,
  deckelements: PropTypes.array,
  data: PropTypes.array,

  // Some switches to make things easier
  stylechooser: PropTypes.bool,
  lockPan: PropTypes.bool,
  lockRotation: PropTypes.bool,
  lockZoom: PropTypes.bool,
  lockToBounds: PropTypes.bool,
  hideNavControls: PropTypes.bool,

  minPitch: PropTypes.number,
  maxPitch: PropTypes.number,
  pitch: PropTypes.number,

  maxBounds: PropTypes.array,
  zoomBounds: PropTypes.array,
  InnerBounds: PropTypes.array,

  mapStyles: PropTypes.array,

  // Just some default props we probably won't set.
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  zoom: PropTypes.number,

  onMapDataEvent: PropTypes.func,
  onViewportChange: PropTypes.func

  // We get these props from calculating the window size at a higher level
  // size: PropTypes.shape({
  //   width: PropTypes.number.isRequired,
  //   height: PropTypes.number.isRequired
  // })
}
BaseMap.defaultProps = {
  stylechooser: true,
  zoom: 12,
  latitude: 49.3050394,
  longitude: -123.1398254,
  maxZoom: 18,
  minZoom: 1,
  mapStyles: MapStyles,
  lockToBounds: true
}

export default BaseMap

const useMap = ({
  lockToBounds, lockPan, lockZoom, lockRotation, maxBounds, latitude, longitude, zoomBounds,
  onViewportChange: incomingOnViewportChange,
  zoom, pitch, minPitch, maxPitch, bearing, size
}) => {
  const [currStyle, setCurrStyle] = useState(0)
  const [viewport, setViewport] = useState({
    latitude: maxBounds ? (maxBounds[1] + maxBounds[3]) / 2 : latitude,
    longitude: maxBounds ? (maxBounds[0] + maxBounds[2]) / 2 : longitude,
    zoom: zoom,
    minPitch: minPitch,
    maxPitch: maxPitch,
    bearing: bearing || 0,
    pitch: pitch || 0
  })

  // const recalcZoomBounds = () => {
  //   // On the first run we're going to try to center the bounds.
  //   const { width, height } = size
  //   const [minlng, minlat, maxlng, maxlat] = maxBounds

  //   try {
  //     const wmview = new WebMercatorViewport({ width: width > 0 ? width : size.width, height: height > 0 ? height : size.height })
  //     console.log('before boundschange', { wmview, bds: [ [minlng, minlat], [maxlng, maxlat] ] })
  //     const fittedBounds = wmview.fitBounds(
  //       [ [minlng, minlat], [maxlng, maxlat] ],
  //       { padding: 20, offset: [0, -40] }
  //     )
  //     const newLat = fittedBounds.latitude
  //     const newLong = fittedBounds.longitude
  //     zoom = fittedBounds.zoom

  //     return [
  //       viewport.zoom + 6,
  //       viewport.zoom - 6
  //     ]
  //   }
  //   catch (e) {
  //     // Sometimes we don't have a valid bound. Don't make a big deal out of it.
  //     console.error(e)
  //   }
  // }

  // const zoomBounds = useMemo(() => recalcZoomBounds(), [maxBounds, size])

  const onViewportChange = newViewport => {
    // Here we set/reset the bounds
    if (lockPan) {
      newViewport.longitude = viewport.longitude
      newViewport.latitude = viewport.latitude
    }
    else if (maxBounds && lockToBounds) {
      if (newViewport.longitude < maxBounds[0]) newViewport.longitude = maxBounds[0]
      else if (newViewport.longitude > maxBounds[2]) newViewport.longitude = maxBounds[2]

      if (newViewport.latitude < maxBounds[1]) newViewport.latitude = maxBounds[1]
      else if (newViewport.latitude > maxBounds[3]) newViewport.latitude = maxBounds[3]
    }
    // Here we set the max and min zoom bounds.
    if (lockZoom) {
      newViewport.zoom = viewport.zoom
    }
    if (lockRotation) {
      newViewport.pitch = viewport.pitch
      newViewport.bearing = viewport.bearing
    }
    // else if (zoomBounds) {
    //   if (newViewport.zoom < zoomBounds.min) newViewport.zoom = zoomBounds.min
    //   else if (newViewport.zoom > zoomBounds.max) newViewport.zoom = zoomBounds.max
    // }
    // console.log('onViewportChange', newViewport)
    if (typeof incomingOnViewportChange === 'function') incomingOnViewportChange(newViewport)
    setViewport(newViewport)
  }

  useEffect(() => {
    // onViewportChange(viewport)
  })

  return [viewport, currStyle, setCurrStyle, onViewportChange]
}
