import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import exifParser from 'exif-parser'
import moment from 'moment'
import PhotoClass from './lib/Photo'
import { useDeviceOrientation, useGeolocation } from './lib/sensors'
import SeriesClass from './lib/Series'
import { SeriesConfig } from './contexts'
import PhotoReviewTool from './views/PhotoReviewTool/PhotoReviewTool'
import CameraTool from './views/CameraTool/CameraTool'
// import PhotoViewTool from './views/PhotoViewTool/PhotoViewTool'
import NewPhotoInfo from './components/NewPhotoInfo/NewPhotoInfo'
import useFileInput from './lib/useFileInput'

const MODES = {
  CAMERA: 'camera',
  FILEPICKER: 'library',
  PHOTOREVIEW: 'photoreview',
  PHOTOINFOEDIT: 'photoinfoedit'
}

const NewPhoto = ({ inputMethod, onCreate, onCancel, photos, isNewSeries }) => {
  const seriesConfig = useContext(SeriesConfig)
  const [newPhoto, setNewPhoto] = useState(null)
  // If series isn't provided then this is a new series and we need to accomodate
  // onSeriesCreate
  const [newSeries, setNewSeries] = useState(photos ? null : new SeriesClass(seriesConfig).series)
  const [photoRefId, setPhotoRefId] = useState(null)
  const [mode, _changeMode] = useState({ current: inputMethod, history: [] })
  const orientation = useDeviceOrientation()
  const geo = useGeolocation()

  const onLibraryAdd = (data) => {
    console.log('onLibraryAdd Debug GOT DATA')
    let exif = {}
    // const regex = /^data:.+\/(.+);base64,(.*)$/
    // const matches = data.match(regex)

    // const parser = exifParser.create(Buffer.from(matches[2], 'base64'))
    // try {
    //   exif = parser.parse()
    //   console.log('got EXIF')
    // }
    // catch (err) {
    // // got invalid data, handle error
    //   console.error('EXIF ERROR', err)
    // }
    // Ok, now try to get the datetime off the EXIF
    let date = moment().format('x')
    try {
      date = exif.tags.DateTimeOriginal
    }
    catch (err) {
      console.error('ERROR GETTING TIMESTAMP', err)
    }
    const newPhoto = new PhotoClass(seriesConfig).photo

    if (isNewSeries) {
      let loc = newSeries.location
      try {
        const { latitude, longitude } = newPhoto.rawData.geolocate
        loc = [latitude, longitude]
      }
      catch (e) {
        console.error(`Couldn't get geolocation from sensors`)
      }
      setNewSeries({
        ...newSeries,
        location: loc
      })
    }

    setNewPhoto({
      ...newPhoto,
      date: date,
      rawData: {
        exif
      },
      base64: data
    })
    changeMode(MODES.PHOTOREVIEW)
    console.log('onLibraryAdd FINISHED')
  }

  const fileClick = useFileInput(onLibraryAdd)

  const changeMode = (newModeName) => {
    console.log('NEWPHOTO changeMode', mode, newModeName)
    _changeMode({
      current: newModeName,
      history: [...mode.history, mode.current]
    })
  }
  const lastMode = () => {
    if (mode.history.length > 0) {
      const oldHistory = [...mode.history]
      const oldMode = oldHistory.pop()
      _changeMode({
        current: oldMode,
        history: oldHistory
      })
    }
    else {
      onCancel()
    }
  }

  const onShutter = (data, refId) => {
    const newPhoto = new PhotoClass(seriesConfig).photo
    if (isNewSeries) {
      let loc = newSeries.location
      try {
        const { latitude, longitude } = newPhoto.rawData.geolocate
        loc = [latitude, longitude]
      }
      catch (e) {
        console.error(`Couldn't get geolocation from sensors`)
      }
      setNewSeries({
        ...newSeries,
        location: loc
      })
    }

    setNewPhoto({
      ...newPhoto,
      rawData: {
        orient: orientation,
        geolocate: geo
      },
      base64: data.base64
    })
    setPhotoRefId(refId)
    changeMode(MODES.PHOTOREVIEW)
  }

  const onSave = () => {
    if (isNewSeries) {
      onCreate({
        ...newSeries,
        photos: [newPhoto]
      })
    }
    else {
      onCreate(newPhoto)
    }
  }

  let component = null
  switch (mode.current) {
    case MODES.CAMERA:
      component = <CameraTool
        photos={photos}
        onShutter={onShutter}
        onCancel={onCancel}
      />
      break
    case MODES.FILEPICKER:
      fileClick()
      break
    case MODES.PHOTOREVIEW:
      component = <PhotoReviewTool
        defaultRefPhotoId={photoRefId}
        photos={photos}
        captured={newPhoto}
        onConfirm={() => changeMode(MODES.PHOTOINFOEDIT)}
        onCancel={onCancel}
        onRetry={lastMode}
      />
      break
    case MODES.PHOTOINFOEDIT:
      component = <NewPhotoInfo
        photo={newPhoto}
        series={newSeries}
        isNewSeries={isNewSeries}
        onChange={isNewSeries ? setNewSeries : setNewPhoto}
        onCancel={onCancel}
        onSave={onSave}
      />
      break
    default:
      console.log('NEWPHOTO: default state. you should NEVER see this', mode)
      onCancel()
  }

  return component
}

NewPhoto.propTypes = {
  inputMethod: PropTypes.oneOf(['camera', 'library']),
  onCreate: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  isNewSeries: PropTypes.bool,
  // This might be a new series so we might have
  // actions to work on that
  // This will be provided EITHER photos or onSeriesCreate
  photos: PropTypes.array
}

export default NewPhoto
