import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { CssBaseline,
  Button,
  Typography,
  AppBar,
  Toolbar,
  IconButton,
  Badge,
  Chip
} from '@material-ui/core'
import {
  ArrowBack as ArrowBackIcon,
  Info as InfoIcon,
  Label as LabelIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import PhotoInfo from '../../components/PhotoInfo/PhotoInfo'
import style from './PhotoViewTool.style'
import Photo from '../../components/Photo/Photo'
import { SeriesConfig } from '../../contexts'
import Tags from '../../lib/Tags'

const STATES = {
  VIEWPIC: 0,
  SHOWINFO: 1,
  SHOWTAGS: 2,
  EDITTAGS: 3,
  FIRSTAGEDIT: 4
}

const PhotoViewTool = ({ classes, photo, onMenuBack, onUpdatePhoto, allowEdit, newPhoto }) => {
  const seriesConfig = useContext(SeriesConfig)
  // This is a special copy of the photo so we can play around with the XY tags and undo
  // if we need to
  const [tempPhoto, setTempPhoto] = useState(photo)
  const [mode, setMode] = useState({
    current: newPhoto ? STATES.FIRSTAGEDIT : STATES.VIEWPIC,
    last: null
  })

  const tagsClass = new Tags(seriesConfig, tempPhoto.tags)

  const changeMode = (desired) => {
    setMode({ current: desired, last: mode.current })
  }

  const onCommitTags = () => {
    changeMode(STATES.SHOWTAGS)
    onUpdatePhoto(tempPhoto)
  }

  const onCommitTagsFirst = () => {
    onUpdatePhoto(tempPhoto)
    onMenuBack()
  }

  const onCancelTagEdit = () => {
    changeMode(STATES.SHOWTAGS)
    setTempPhoto(photo)
  }

  const photoClick = (e) => {
    if (mode.current === STATES.VIEWPIC) {
      e.preventDefault()
      e.stopPropagation()
      changeMode(STATES.SHOWINFO)
    }
  }

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <AppBar position='static' className={classes.appBar}>
        <Toolbar>
          {mode.current !== STATES.FIRSTAGEDIT && (
            <IconButton
              className={classes.menuButton}
              color='inherit'
              aria-label='Menu'
              onClick={() => onMenuBack()}
            >
              <ArrowBackIcon />
            </IconButton>
          )}

          <Typography variant='h6' color='inherit' className={classes.grow}>
            {newPhoto ? 'Add Tags' : 'Photo View'}
          </Typography>
          { mode.current === STATES.EDITTAGS &&
          <Button color='inherit' onClick={onCancelTagEdit}>
            Cancel
          </Button>
          }

          { mode.current === STATES.EDITTAGS &&
          <Button color='inherit' onClick={onCommitTags}>
            Save
          </Button>
          }
          { mode.current === STATES.FIRSTAGEDIT &&
          <Button color='inherit' onClick={onCommitTagsFirst}>
            Next
          </Button>
          }

          { allowEdit && mode.current === STATES.SHOWTAGS &&
          <Button color='inherit' onClick={() => changeMode(STATES.EDITTAGS)}>
            Edit
          </Button>
          }

          { (mode.current !== STATES.EDITTAGS && mode.current !== STATES.FIRSTAGEDIT) &&
          <IconButton color='inherit'
            className={classNames(classes.tagButton, mode.current === STATES.SHOWTAGS && classes.activeButton)}
            onClick={() => changeMode(mode.current === STATES.SHOWTAGS ? STATES.VIEWPIC : STATES.SHOWTAGS)}
          >
            <Badge className={classes.margin} badgeContent={tagsClass.getXYTags().length} color='secondary'>
              <LabelIcon />
            </Badge>
          </IconButton>
          }

          { (mode.current !== STATES.EDITTAGS && mode.current !== STATES.FIRSTAGEDIT) &&
          <IconButton color='inherit'
            className={classNames(classes.infoButton, mode.current === STATES.SHOWINFO && classes.activeButton)}
            onClick={() => changeMode(mode.current === STATES.SHOWINFO ? STATES.VIEWPIC : STATES.SHOWINFO)}
          >
            <InfoIcon />
          </IconButton>
          }

        </Toolbar>
      </AppBar>
      <div className={classes.inner} onClick={photoClick}>
        <Photo
          showTags={mode.current === STATES.SHOWTAGS}
          photo={tempPhoto}
          edit={allowEdit && (mode.current === STATES.EDITTAGS || mode.current === STATES.FIRSTAGEDIT)}
          onChange={setTempPhoto}
          onCommitTags={onCommitTags}
        />
        {mode.current === STATES.SHOWINFO && (
          <div className={classes.infoWindow}>
            Esse nisi adipisicing nulla deserunt ut adipisicing officia fugiat culpa excepteur amet et mollit magna. Voluptate cupidatat deserunt est ea esse duis. Aute cillum pariatur pariatur magna consectetur excepteur. Sit pariatur nostrud labore ex mollit mollit nisi reprehenderit. Id in sint commodo commodo ipsum fugiat aliqua culpa velit ad sint ut. Magna ut nostrud non do minim. Consequat ex duis elit eu cillum pariatur in nostrud est reprehenderit.
          </div>
        )}
      </div>
      <PhotoInfo
        open={mode.current === STATES.SHOWINFO}
        photo={photo}
        allowEdit={allowEdit}
        onChange={onUpdatePhoto}
        onClose={() => changeMode(mode.last)}
      />
      { (mode.current === STATES.EDITTAGS || mode.current === STATES.FIRSTAGEDIT) && (
        <Chip
          label='Tap to tag features...'
          onClick={() => {}}
          className={classes.tipChip}
        />
      )}

    </div>
  )
}

PhotoViewTool.propTypes = {
  classes: PropTypes.object.isRequired,
  photo: PropTypes.object.isRequired,
  allowEdit: PropTypes.bool,
  newPhoto: PropTypes.bool,
  // Now some action functions
  onMenuBack: PropTypes.func.isRequired,
  onUpdatePhoto: PropTypes.func.isRequired
}

export default withStyles(style)(PhotoViewTool)
