import React, { useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import {
  Divider,
  List,
  ListItem,
  ListItemText,
  Typography
} from '@material-ui/core'
import { formatDate } from '../../lib/datelib'
import { withStyles } from '@material-ui/core/styles'
import ExpandNote from '../controls/ExpandNote'
import TagChip from '../TagChip'
import style from './SeriesInfo.style'
import SeriesConfigClass from '../../lib/SeriesConfig'
import { SeriesConfig } from '../../contexts'

const SeriesInfo = ({ classes, series }) => {
  const seriesConfig = useContext(SeriesConfig)
  const seriesConfigInst = new SeriesConfigClass(seriesConfig)
  const newTags = series.photos.reduce((acc, { tags }) => {
    tags.forEach(tag => {
      if (!acc[tag.typeId]) {
        acc[tag.typeId] = {
          [tag.label]: { ...tag, count: 1 }
        }
      }
      else if (acc[tag.typeId][tag.label]) {
        const existing = acc[tag.typeId][tag.label]
        acc[tag.typeId][tag.label] = { ...existing, count: existing.count + 1 }
      }
      else acc[tag.typeId][tag.label] = { ...tag, count: 1 }
    })
    return acc
  }, {})

  return (
    <div className={classes.root}>
      <Typography className={classes.heading}>Series Name</Typography>
      <Divider />
      <Typography variant='body1' color='inherit' className={classNames(classes.title, classes.textField)}>
        {series.name}
      </Typography>

      <Typography className={classes.heading}>Note</Typography>
      <Divider />
      <ExpandNote className={classNames(classes.textField, classes.note)} text={series.note} />

      <Typography className={classes.heading}>Series Summary</Typography>
      <Divider />
      <List className={classes.list}>
        <ListItem>
          <ListItemText
            primary={`Photos: ${series.photos.length}`}
            secondary={series.photos.length > 0 && (
              <span>
             First: {formatDate(series.photos[series.photos.length - 1].date)}
                <br />
             Latest: {formatDate(series.photos[0].date)}
              </span>
            )}
          />
        </ListItem>
        <ListItem>
          <ListItemText>
            <Typography>Tags in use (across all photos)</Typography>
            {Object.keys(newTags).map((tagKey, idtk) => (
              <div key={`tagchipkey${idtk}`}>
                <Typography className={classes.heading} variant='overline'>{tagKey}</Typography>
                <Divider />
                {
                  Object.values(newTags[tagKey]).map((tag, idx) => (
                    <TagChip key={`tagchip-${idtk}-${idx}`} tag={tag} group={seriesConfigInst.getTagTypeIndex(tagKey)} />
                  ))
                }
              </div>
            ))}
          </ListItemText>
        </ListItem>
      </List>

      <Typography className={classes.heading}>Metadata</Typography>
      <Divider />
      <List className={classes.list}>
        {Object.keys(series.meta).map((key, idx) => seriesConfig.meta[key] ? (
          <ListItem key={`metadata${idx}`}>
            <ListItemText
              primary={seriesConfig.meta[key].label}
              secondary={seriesConfig.meta[key].render(series.meta[key])}
            />
          </ListItem>
        ) : null
        )}
      </List>

    </div>
  )
}

SeriesInfo.propTypes = {
  classes: PropTypes.object.isRequired,
  series: PropTypes.object.isRequired
}

export default withStyles(style)(SeriesInfo)
