let { useCallback, useState, useLayoutEffect } = require('react')

const getSize = el => ({
  width: !el ? 0 : el.offsetWidth,
  height: !el ? 0 : el.offsetHeight
})

const useComponentSize = ref => {
  let [ComponentSize, setComponentSize] = useState(
    getSize(ref ? ref.current : {})
  )

  const handleResize = useCallback(() => {
    if (ref.current) setComponentSize(getSize(ref.current))
  }, [ref])

  useLayoutEffect(() => {
    if (!ref.current) return

    handleResize()

    if (typeof ResizeObserver === 'function') {
      let resizeObserver = new ResizeObserver(() => handleResize())
      resizeObserver.observe(ref.current)

      return () => {
        resizeObserver.disconnect(ref.current)
        resizeObserver = null
      }
    }
    else {
      window.addEventListener('resize', handleResize)

      return () => {
        window.removeEventListener('resize', handleResize)
      }
    }
  },
  [ref.current])

  return ComponentSize
}

export default useComponentSize
