
import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  CssBaseline,
  AppBar,
  Toolbar,
  Button,
  Typography,
  FormLabel
} from '@material-ui/core'
import {
  Delete as DeleteIcon,
  ArrowBack as ArrowBackIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import Slider from '@material-ui/lab/Slider'
import TextAreaControl from '../../components/controls/TextAreaControl'
import TextFieldControl from '../../components/controls/TextFieldControl'
import MapDot from '../../components/maps/MapDot/MapDot'
import DeleteDialog from '../../components/dialogs/DeleteDialog'
import style from './SeriesEdit.style'

const SeriesEdit = ({ classes, series, onMenuBack, onChange, onDeleteSeries }) => {
  const [rotation, setRotation] = useState(series.direction || -1)
  const [deleteConfirm, setDeleteConfirm] = useState(false)

  const onChangeFormState = (name, value) => {
    onChange({
      ...series,
      [name]: value
    })
  }

  const onRotationChange = (dir) => {
    const newDir = dir >= 0 ? dir : null
    onChangeFormState('direction', newDir)
    setRotation(newDir)
  }

  const onMapChange = ({ latitude, longitude }) => {
    onChangeFormState('location', [latitude, longitude])
  }

  const onDeleteConfirm = (confirmation) => {
    setDeleteConfirm(false)
    if (confirmation === true) onDeleteSeries(series.id)
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='static' className={classes.appBar}>
        <Toolbar>
          <Typography variant='h6' color='inherit' className={classes.grow}>
           Edit Series Details
          </Typography>
          <Button className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            Ok
          </Button>
        </Toolbar>
      </AppBar>
      <div className={classes.inner}>
        <form className={classes.form} elevation={1} noValidate autoComplete='off'>
          <TextFieldControl
            className={classes.field}
            label='Series Name'
            placeholder='New Series'
            permaEdit
            allowEdit
            defaultValue={series.name}
            onChange={val => onChangeFormState('name', val)}
          />
          <TextAreaControl
            className={classes.field}
            label='Notes'
            placeholder='Any instructions for the next person to add a picture to this series?'
            multiline
            rows={1}
            rowsMax={4}
            permaEdit
            allowEdit
            defaultValue={series.note}
            onChange={val => onChangeFormState('note', val)}
          />

          <div className={classes.mapWrapper}>
            <FormLabel className={classes.formLabel}>Location</FormLabel>
            <div style={{ height: 200, marginBottom: 10 }}>
              <MapDot
                editable
                className={classes.map}
                direction={rotation > 0 ? rotation : null}
                latitude={series.location[0]}
                longitude={series.location[1]}
                onChange={onMapChange}
              />
            </div>
            <FormLabel className={classes.mapFormLabel}>Direction {rotation > 0 ? '' : '(off)'}</FormLabel>
            <Slider
              classes={{ container: classes.slider }}
              value={rotation || -1}
              min={-1}
              max={360}
              step={1}
              onChange={(e, value) => onRotationChange(value)}
            />
          </div>
          {/* <TextField
            id='standard-name'
            label='Name'
            className={classes.textField}
            value={series.name}
            permaEdit
            onChange={e => onChangeFormState('name', e.target.value)}
            margin='dense'
          />
          <TextField
            id='standard-multiline-flexible'
            label='Notes'
            multiline
            placeholder='This photo is cool because...'
            rows='4'
            rowsMax='7'
            value={series.note}
            onChange={e => onChangeFormState('note', e.target.value)}
            className={classes.textField}
            margin='dense'
          />

          <div className={classes.mapWrapper}>
            <FormLabel className={classes.formLabel}>Location</FormLabel>
            <div style={{ height: 200 }}>
              <MapDot
                editable
                className={classes.map}
                direction={rotation > 0 ? rotation : null}
                latitude={series.location[0]}
                longitude={series.location[1]}
                onChange={onMapChange}
              />
            </div>
            <FormLabel className={classes.formLabel}>Direction {rotation > 0 ? '' : '(off)'}</FormLabel>
            <Slider
              classes={{ container: classes.slider }}
              value={rotation}
              min={-1}
              max={360}
              step={1}
              onChange={(e, value) => onRotationChange(value)}
            />
          </div> */}
        </form>
      </div>
      <DeleteDialog
        open={deleteConfirm}
        onConfirm={onDeleteConfirm}
        title='Delete Series'
        message='Are you sure you want to delete this series? The record and all images associated with it will be gone forever.'
      />
    </div>
  )
}

SeriesEdit.propTypes = {
  classes: PropTypes.object.isRequired,
  series: PropTypes.object.isRequired,
  // now some functions
  onMenuBack: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onDeleteSeries: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesEdit)
