import React, { useState } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import { Typography } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    position: 'relative',
    display: 'block',
    overflow: 'hidden',
    whiteSpace: 'pre-line',
    paddingBottom: theme.spacing.unit,
    transition: theme.transitions.create(['all'], {
      easing: theme.transitions.easing.easeInOut,
      duration: theme.transitions.duration.complex
    }),
    '&::before': {
      content: '"  "',
      display: 'block',
      position: 'absolute',
      bottom: 0,
      width: '100%',
      height: 100,
      background: `linear-gradient(180deg, rgba(255,255,255,0) 0%, ${theme.palette.background.paper} 100%)`
    }
  },
  expanded: {
    '&::before': {
      opacity: 0
    }
  }
})

const ExpandNote = ({ classes, text, height }) => {
  const [expandNote, setExpandNote] = useState(false)
  return (
    <Typography
      onClick={() => setExpandNote(!expandNote)}
      variant='body1'
      color='inherit'
      style={{ maxHeight: expandNote ? 5000 : height, minHeight: height }}
      className={classNames(classes.root, expandNote && classes.expanded)}
    >
      {text}
    </Typography>
  )
}

ExpandNote.propTypes = {
  classes: PropTypes.object.isRequired,
  text: PropTypes.string,
  height: PropTypes.number
}
ExpandNote.defaultProps = {
  height: 100
}

export default withStyles(style)(ExpandNote)
