import React, { useState } from 'react'
import PropTypes from 'prop-types'
// import classNames from 'classnames'
import {
  CssBaseline,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Tabs,
  Tab
} from '@material-ui/core'
import {
  ArrowBack as ArrowBackIcon,
  Slideshow as SlideshowIcon,
  AddAPhoto as AddCameraIcon,
  MoreVert as MoreIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import ContextMenu from '../../components/ContextMenu'
import MapDot from '../../components/maps/MapDot/MapDot'
import useNewImageMenu from '../../components/controls/useNewImageMenu'

import SeriesInfo from '../../components/SeriesInfo/SeriesInfo'
import SwipeShow from '../../components/SwipeShow/SwipeShow'
import PhotoGrid from '../../components/PhotoGrid/PhotoGrid'
import DeleteDialog from '../../components/dialogs/DeleteDialog'
import style from './SeriesView.style'

const SeriesView = ({
  classes, series, onSlideshowStart, onChange, allowEdit,
  onEditSeries, onDeletePhotos, onCameraAdd,
  onDeleteSeries, onLibraryAdd, onTileClick, onMenuBack
}) => {
  const [contextEl, setContextEl] = useState()
  const [deleteConfirm, setDeleteConfirm] = useState(false)

  const { onDownThunk, onUpThunk, onBlock, menu } = useNewImageMenu(
    () => onCameraAdd(series),
    onLibraryAdd
  )

  const onDeleteConfirm = (confirmation) => {
    setDeleteConfirm(false)
    if (confirmation === true) onDeleteSeries(series.id)
  }

  const [listTypeTab, setListTypeTab] = useState(0)
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='static' className={classes.appBar}>
        <Toolbar>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='h6' color='inherit' className={classes.grow}>
           Series
          </Typography>
          {allowEdit && (
            <IconButton
              onClick={onBlock}
              onMouseDown={onDownThunk(series)}
              onMouseUp={onUpThunk(series)}
              onTouchStart={onDownThunk(series)}
              onTouchEnd={onUpThunk(series)}
              className={classes.addPhotoButton}
              color='inherit'
              aria-label='Menu'
            >
              <AddCameraIcon />
            </IconButton>
          )}
          <IconButton
            className={classes.startSlideshowButton}
            disabled={series.photos.length === 0}
            color='inherit'
            aria-label='Menu'
            onClick={onSlideshowStart}
          >
            <SlideshowIcon />
          </IconButton>
          { allowEdit && (
            <IconButton
              aria-haspopup='true'
              color='inherit'
              onClick={event => setContextEl(event.currentTarget)}
            >
              <MoreIcon />
            </IconButton>
          )}

          <ContextMenu anchorEl={contextEl} onClose={() => setContextEl(null)} menuItems={[
            { text: 'Edit Series', action: onEditSeries },
            { text: 'Delete Series', action: () => setDeleteConfirm(true) }
          ]} />
          {menu}
        </Toolbar>
        <Tabs variant='fullWidth'
          value={listTypeTab}
          onChange={(e, value) => setListTypeTab(value)}
        >
          <Tab label='Info' />
          <Tab label='Map' />
          <Tab label='Photos' />
        </Tabs>
      </AppBar>

      <div className={classes.inner}>
        {listTypeTab === 0 && (
          <React.Fragment>
            {series.photos.length > 0 && (
              <SwipeShow
                photos={series.photos}
                onImageSelect={onTileClick}
              />
            )}
            <SeriesInfo series={series} onChange={onChange} />
          </React.Fragment>
        )}
        {listTypeTab === 2 && (
          <PhotoGrid
            photos={series.photos}
            allowEdit={allowEdit}
            onTileClick={onTileClick}
            onDelete={onDeletePhotos}
          />
        )}
      </div>

      {listTypeTab === 1 && (
        <MapDot
          zoom={18}
          longitude={series.location[1]}
          latitude={series.location[0]}
          direction={series.direction}
        />
      )}
      <DeleteDialog
        open={deleteConfirm}
        onConfirm={onDeleteConfirm}
        title='Delete Series'
        message='Are you sure you want to delete this series? The record and all images associated with it will be gone forever.'
      />
    </div>
  )
}

SeriesView.propTypes = {
  classes: PropTypes.object.isRequired,
  series: PropTypes.object,
  allowEdit: PropTypes.bool,
  // Action functions
  onSlideshowStart: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onDeletePhotos: PropTypes.func.isRequired,
  onDeleteSeries: PropTypes.func.isRequired,
  onCameraAdd: PropTypes.func.isRequired,
  onEditSeries: PropTypes.func.isRequired,
  onLibraryAdd: PropTypes.func.isRequired,
  onTileClick: PropTypes.func.isRequired,
  onMenuBack: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesView)
