import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import {
  Dialog,
  DialogTitle,
  Typography
} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import XYTagSelect from './XYTagSelect'
import style from './XYTagChooser.style'
import { SeriesConfig } from '../../../contexts'

const XYTagChooser = ({ classes, coords, open, onNewTag, onClose }) => {
  const seriesConfig = useContext(SeriesConfig)
  const xyTags = seriesConfig.tags.filter(t => t.isXY)
  if (!coords) return null
  return (
    <Dialog
      open={open}
      scroll='paper'
      classes={{ paperScrollPaper: classes.root }}
      onClose={() => onClose()}
      aria-labelledby='simple-dialog-title'
    >
      <DialogTitle id='simple-dialog-title' className={classes.header}>New Tag:</DialogTitle>
      <div className={classes.dlgBody}>
        {xyTags.length > 1 && (
          <Typography variant='caption'>What kind of tag would you like to create?</Typography>
        )}

        {xyTags.map((tagType, idx) => (
          <div className={classes.listItem} key={`tagSelect${idx}`}>
            <XYTagSelect tagConfig={tagType} onChange={onNewTag(coords, tagType)} />
          </div>
        ))}
      </div>
    </Dialog>
  )
}

XYTagChooser.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  coords: PropTypes.object,
  onClose: PropTypes.func.isRequired,
  onNewTag: PropTypes.func.isRequired
}

export default withStyles(style)(XYTagChooser)
