import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  Typography,
  TextField
} from '@material-ui/core'
import useFormFieldState from './useFormFieldState'
// import ExpandNote from './ExpandNote'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
  },
  rootEdit: {
  },
  textBody: {
    whiteSpace: 'pre-line',
    padding: theme.spacing.unit
  },
  textField: {
    '& textarea': {
      color: theme.palette.grey['900'],
      background: theme.palette.grey['200'],
      padding: theme.spacing.unit
    }

  }
})

const TextFieldControl = ({
  classes, defaultValue,
  className, label,
  hint, permaEdit,
  placeholder,
  allowEdit,
  onChange: onChangeExt
}) => {
  const { editing, value, onChange, Header } = useFormFieldState(defaultValue, allowEdit, onChangeExt, permaEdit)

  return (
    <div className={classNames(classes.root, editing && classes.rootEdit, className)}>
      <Header>{label}</Header>
      {!editing && !permaEdit
        ? <div className={classes.textBody}>
          <Typography variant='body2'>{value}</Typography>
        </div>
        : <TextField
          fullWidth
          placeholder={placeholder}
          value={value || ''}
          className={classes.textField}
          onChange={(e) => onChange(e.target.value)}
        />
      }
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>

  )
}

TextFieldControl.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  label: PropTypes.string.isRequired,
  defaultValue: PropTypes.string,
  hint: PropTypes.string,
  allowEdit: PropTypes.bool,
  permaEdit: PropTypes.bool,
  // These come in from the HOC
  onChange: PropTypes.func.isRequired
}

export default withStyles(style)(TextFieldControl)
