import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import {
  Button,
  Dialog,
  IconButton,
  DialogTitle,
  DialogContent,
  Typography,
  DialogActions,
  Divider,
  List,
  ListItem,
  ListItemText
} from '@material-ui/core'
import TagClass from '../../lib/Tags'
import { Close as CloseIcon } from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import TagControl from '../controls/TagControl'
import TextAreaControl from '../controls/TextAreaControl'
import DateControl from '../controls/DateControl'
import style from './PhotoInfo.style'
import { SeriesConfig } from '../../contexts'

// import DateControl from './controls/DateControl'
const PhotoInfo = ({ classes, allowEdit, photo, onClose, open, onChange }) => {
  const seriesConfig = useContext(SeriesConfig)
  const onChangeLocal = (fieldName) => (val) => {
    onChange({ ...photo, [fieldName]: val })
  }

  const tagInst = new TagClass(seriesConfig, photo.tags)

  return (
    <Dialog
      fullScreen
      fullWidth
      onClose={onClose}
      aria-labelledby='customized-dialog-title'
      open={open}
    >

      <DialogTitle disableTypography className={classes.dlgTitleContainer}>
        <Typography variant='title' className={classes.dlgTitle}>
          Photo Properties
        </Typography>
        <IconButton onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <DateControl
          className={classes.field}
          label='Photo taken on'
          defaultValue={photo.date}
          onChange={onChangeLocal('date')}
          allowEdit={allowEdit}
        />

        <TextAreaControl
          label='Photo Note'
          defaultValue={photo.note}
          collapsed
          collapsedHeight={50}
          onChange={onChangeLocal('note')}
          allowEdit={allowEdit}
        />

        <Typography variant='subtitle1'>Category tags</Typography>

        {tagInst.seriesConfig.getNonXYTagTypes().map((tt, idx) => (
          <TagControl
            className={classes.field}
            defaultValue={photo.tags}
            tagConfig={tt}
            group={tagInst.seriesConfig.getTagTypeIndex(tt.id)}
            allowEdit={allowEdit}
            onChange={onChangeLocal('tags')}
            key={`tagtype-${idx}`}
          />
        ))}

        <Typography className={classes.heading}>Metadata</Typography>
        <Divider />
        <List className={classes.list}>
          {Object.keys(photo.meta).map((key, idx) => seriesConfig.meta[key] ? (
            <ListItem key={`metadata${idx}`}>
              <ListItemText
                primary={seriesConfig.meta[key].label}
                secondary={seriesConfig.meta[key].render(photo.meta[key])}
              />
            </ListItem>
          ) : null
          )}
        </List>

      </DialogContent>

      <DialogActions>
        <Button onClick={onClose} color='primary'>
        Back
        </Button>
      </DialogActions>
    </Dialog>
  )
}

PhotoInfo.propTypes = {
  classes: PropTypes.object.isRequired,
  allowEdit: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  photo: PropTypes.object.isRequired,
  // Now some action tags
  onChange: PropTypes.func.isRequired
}

export default withStyles(style)(PhotoInfo)
