import React, { useState, useRef } from 'react'
import PropTypes from 'prop-types'
import {
  Menu,
  MenuItem
} from '@material-ui/core'
import {
  AddAPhoto as AddCameraIcon,
  PhotoLibrary as PhotoLibraryIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'

const useNewImageMenu = (cameraClick, libraryClick) => {
  const [menuEl, setMenuEl] = useState(null)
  const [series, setSeries] = useState(null)
  const [down, setDown] = useState(false)
  const downRef = useRef(down)
  downRef.current = down

  const startTimer = (el) => {
    setDown(true)
    setTimeout(() => {
      if (downRef && downRef.current) setMenuEl(el)
    }, 300)
  }

  const onDownThunk = series => (e) => {
    // console.log('onDown', e.currentTarget)
    setSeries(series)
    startTimer(e.currentTarget)
  }

  const onUpThunk = series => (e) => {
    // console.log('onUp', e.currentTarget)
    setSeries(series)
    setDown(false)
    cameraClick(series)
  }

  const onCamera = (e) => {
    // console.log('onCamera', e.currentTarget)
    e.stopPropagation()
    e.preventDefault()
    cameraClick(series)
    setMenuEl(null)
  }

  const onLibrary = (e) => {
    // console.log('onLibrary', e.currentTarget)
    e.stopPropagation()
    e.preventDefault()
    libraryClick(series)
    setMenuEl(null)
  }

  const onClose = (e) => {
    // console.log('Close Menu', e.currentTarget)
    e.stopPropagation()
    e.preventDefault()
    setMenuEl(null)
  }

  const onBlock = (e) => {
    // console.log('Block', e.currentTarget)
    e.stopPropagation()
    e.preventDefault()
  }

  return {
    onDownThunk,
    onUpThunk,
    onBlock,
    menu: (
      <CameraPopup {...{ menuEl, onCamera, onLibrary, onClose }} />
    )
  }
}

export default useNewImageMenu

const CameraPopupUnstyled = ({ classes, menuEl, onCamera, onLibrary, onClose }) => (
  <Menu
    id='simple-menu'
    anchorEl={menuEl}
    open={Boolean(menuEl)}
    className={classes.root}
    onClose={onClose}
  >
    <MenuItem onMouseUp={onCamera} onTouchEnd={onCamera}>
      <AddCameraIcon />Add from camera
    </MenuItem>
    <MenuItem onMouseUp={onLibrary} onTouchEnd={onLibrary}>
      <PhotoLibraryIcon /> Add from Library
    </MenuItem>
  </Menu>
)

CameraPopupUnstyled.propTypes = {
  classes: PropTypes.object.isRequired,
  menuEl: PropTypes.object,
  onCamera: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  onLibrary: PropTypes.func.isRequired
}

const style = theme => ({
  root: {

  }
})

export const CameraPopup = withStyles(style)(CameraPopupUnstyled)
