import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import {
  CssBaseline,
  AppBar,
  Button,
  IconButton,
  Toolbar,
  Typography,
  Dialog,
  FormControlLabel,
  Switch,
  Divider,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import SeriesList from '../views/SeriesList/SeriesList'
import RepeatablePhotos from '../RepeatablePhotos'
import seriesConfig from './seriesConfig'

import style from './TestApp.style'
import {
  Info as InfoIcon,
  Settings as SettingsIcon,
  PlaylistAdd as PlaylistAddIcon
  // Refresh as RefreshIcon
} from '@material-ui/icons'
import db from './db'
import useNewImageMenu from '../components/controls/useNewImageMenu'

const TestApp = ({ classes }) => {
  const [currSeriesList, setSeriesList] = useState([])
  const [aboutOpen, setAboutOpen] = useState(false)
  const [allowEdit, setAllowEdit] = useState(true)
  const [settingsOpen, setSettingsOpen] = useState(false)

  // {SERIES, MODE}
  const [rpState, setStartState] = useState(null)

  const onChange = (newSeries) => {
    // console.log('TestApp:: db.put', newSeries)
    setSeriesList(currSeriesList.map(s => {
      if (s.id !== newSeries.id) return s
      else return newSeries
    }))
    setStartState({
      ...rpState,
      series: newSeries
    })
    db.set(newSeries.id, newSeries).then(fetchData)
  }

  const onDelete = (id) => {
    db.delete(id).then(fetchData)
    fetchData()
    setStartState(null)
  }

  // Now some functions for the series list
  const tileClick = (selectedSeries) => setStartState({ series: selectedSeries, modeName: 'series' })
  const cameraClick = (selectedSeries) => {
    setStartState({ series: selectedSeries, modeName: 'newphoto', inputMethod: 'camera' })
  }

  // When you click the library chooser you get this
  const libraryClick = (selectedSeries) => {
    // console.log('libraryClick debug', selectedSeries)
    setStartState({ series: selectedSeries, modeName: 'newphoto', inputMethod: 'library' })
  }

  const seriesCameraClick = () => {
    setStartState({ series: null, modeName: 'newseries', inputMethod: 'camera' })
  }

  // When you click the library chooser you get this
  const seriesLibraryClick = () => {
    // console.log('libraryClick debug', selectedSeries)
    setStartState({ series: null, modeName: 'newseries', inputMethod: 'library' })
  }

  const { onDownThunk, onUpThunk, onBlock, menu } = useNewImageMenu(
    seriesCameraClick,
    seriesLibraryClick
  )

  const onNewSeries = async (series) => {
    console.log('TESTApp onNewSeries', series)
    await db.set(series.id, series)
    setSeriesList([...currSeriesList, series])
    setStartState({ series, modeName: 'series' })
  }

  // This is just for reloading test data in case the users delete it
  const reloadData = () => {
    db.reload()
    fetchData()
    setSettingsOpen(false)
  }

  const dumpData = async () => {
    await db.getAll().then(data => {
      console.log('DUMP\n\n\n', JSON.stringify(data))
    })
  }

  // Can't use async inside a hook so this is a workaround:
  const fetchData = async () => {
    await db.getAll().then(setSeriesList)
  }

  useEffect(() => {
    fetchData()
  }, [])

  if (rpState) {
    return (
      <RepeatablePhotos
        allowEdit={allowEdit}
        seriesConfig={seriesConfig}
        series={rpState.series}
        inputMethod={rpState.inputMethod}
        modeName={rpState.modeName}
        onChange={onChange}
        onDelete={onDelete}
        onNewSeries={onNewSeries}
        onClose={() => setStartState(null)}
      />
    )
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position='static' className={classes.appBar}>
        <Toolbar>
          <Typography variant='h6' color='inherit' className={classes.grow}>
           Photos Demo
          </Typography>
          <IconButton color='inherit' onClick={() => setSettingsOpen(true)}>
            <SettingsIcon />
          </IconButton>
          <IconButton color='inherit' onClick={() => setAboutOpen(true)}>
            <InfoIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <div className={classes.inner}>
        <div className={classes.projMockup}>

          <Typography variant='h5'>My Awesome Project</Typography>
          <Typography paragraph variant='body1'>
        Imagine that this page is a project or other item you'd want to attach repeatable photos to
          </Typography>
        </div>

        <AppBar position='static' color='default' className={classes.photoBar}>
          <Toolbar>
            <Typography variant='subtitle1' color='inherit' className={classes.grow}>
              Repeatable Photo Series:
            </Typography>
            {allowEdit && (
              <IconButton
                color='inherit'
                onClick={onBlock}
                onMouseDown={onDownThunk()}
                onMouseUp={onUpThunk()}
                onTouchStart={onDownThunk()}
                onTouchEnd={onUpThunk()}
              >
                <PlaylistAddIcon />
              </IconButton>
            )}

          </Toolbar>
          {menu}
        </AppBar>
        <SeriesList
          columns={1}
          allowEdit={allowEdit}
          seriesList={currSeriesList}
          tileClick={tileClick}
          cameraClick={cameraClick}
          libraryClick={libraryClick}
        />

        {/*

                SETTINGS DIALOG

        */}
        <Dialog
          open={settingsOpen}
          onClose={() => setSettingsOpen(false)}
          aria-labelledby='form-dialog-title'
        >
          <DialogTitle id='form-dialog-title'>Settings</DialogTitle>
          <DialogContent>

            <FormControlLabel
              fullWidth
              control={
                <Switch
                  checked={allowEdit}
                  onChange={() => setAllowEdit(!allowEdit)}
                  color='primary'
                />
              }
              labelPlacement='start'
              label='Allow Edits?'
            />
            <Typography paragraph variant='caption'>
             Turn this off to simulate what a view-only experience is like.
            </Typography>

            <Button fullWidth variant='contained' color='secondary' className={classes.button} onClick={reloadData}>
                RELOAD DATA
            </Button>

            <Typography paragraph variant='caption'>
              If you have deleted the stock test series click this button to bring them back.
            </Typography>

            <Button fullWidth variant='contained' color='primary' className={classes.button} onClick={dumpData}>
                Dump DB to console
            </Button>

          </DialogContent>
          <DialogActions>
            <Button onClick={() => setSettingsOpen(false)} color='primary'>
              Ok
            </Button>
          </DialogActions>
        </Dialog>

        {/*

                ABOUT DIALOG

        */}
        <Dialog
          open={aboutOpen}
          fullScreen
          onClose={() => setAboutOpen(false)}
          aria-labelledby='form-dialog-title'
        >
          <DialogTitle id='form-dialog-title'>About</DialogTitle>
          <DialogContent>
            <Typography paragraph variant='body1'>
              This is a proof of concept app showing how repeatable photos can work as a plugin in an
              existing app
            </Typography>
            <Divider />
            <Typography paragraph variant='subtitle1'>Photo Series</Typography>
            <Typography paragraph variant='body1'>
              A Photo series is a set of photos taken from the same geographic position and facing the same direction
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setAboutOpen(false)} color='primary'>
              Ok
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  )
}

TestApp.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(style)(TestApp)
