import React from 'react'
import PropTypes from 'prop-types'
import {
  GridList,
  GridListTile,
  GridListTileBar,
  Fab
} from '@material-ui/core'
import {
  AddAPhoto as AddCameraIcon
} from '@material-ui/icons'
import { withStyles } from '@material-ui/core/styles'
import useNewImageMenu from '../../components/controls/useNewImageMenu'
import style from './SeriesList.style'
import { formatDate } from '../../lib/datelib'

const SeriesList = ({ classes, seriesList, allowEdit, tileClick, cameraClick, columns, libraryClick }) => {
  const { onDownThunk, onUpThunk, onBlock, menu } = useNewImageMenu(
    cameraClick,
    libraryClick
  )

  return (
    <GridList cols={columns || 1} cellHeight={200} className={classes.root}>
      {seriesList.map((seriesItem, idx) => {
        // const photo = seriesItem.photos.find(photo => photo.featured === true)
        const sortedPhotos = [...seriesItem.photos]
        sortedPhotos.sort((a, b) => parseInt(b.date, 10) - parseInt(a.date, 10))
        const photo = sortedPhotos[0]
        return (
          <GridListTile key={`gridTile${idx}`} onClick={() => tileClick(seriesItem)}>
            {photo && (
              <img
                src={photo.url || photo.base64}
                alt={seriesItem.name}
              />
            )}
            {allowEdit && (
              <Fab
                onClick={onBlock}
                onMouseDown={onDownThunk(seriesItem)}
                onMouseUp={onUpThunk(seriesItem)}
                onTouchStart={onDownThunk(seriesItem)}
                onTouchEnd={onUpThunk(seriesItem)}
                color='secondary'
                className={classes.fab}
              >
                <AddCameraIcon />
              </Fab>
            )}
            {menu}
            <GridListTileBar
              title={seriesItem.name}
              subtitle={<span>{seriesItem.photos.length} photos <strong>Last Photo</strong>: {formatDate(seriesItem.photos[0].date, 'llll')}</span>}
            />
          </GridListTile>
        )
      })}
    </GridList>
  )
}

SeriesList.propTypes = {
  classes: PropTypes.object.isRequired,
  seriesList: PropTypes.array.isRequired,
  columns: PropTypes.number,
  allowEdit: PropTypes.bool,
  tileClick: PropTypes.func.isRequired,
  cameraClick: PropTypes.func.isRequired,
  libraryClick: PropTypes.func.isRequired
}

export default withStyles(style)(SeriesList)
