import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import {
  TextField,
  Typography
} from '@material-ui/core'
import useFormFieldState from './useFormFieldState'
import { withStyles } from '@material-ui/core/styles'
import { timestampToHTML5Input, HTML5InputToTimestamp } from '../../lib/datelib'

const style = theme => ({
  root: {
  },
  rootEdit: {
  },
  formControl: {
    width: '100%'
  },
  textBody: {
    whiteSpace: 'pre-line'
  },
  actionButtons: {
    textAlign: 'right'
  }
})

const DateControl = ({
  classes,
  label,
  defaultValue,
  permaEdit,
  hint,
  allowEdit,
  onChange: onChangeExt,
  className
}) => {
  const { editing, value, onChange, Header } = useFormFieldState(defaultValue, allowEdit, onChangeExt, permaEdit)
  const disableField = !(editing || permaEdit)
  return (
    <div className={classNames(classes.root, editing && classes.rootEdit, className)}>
      <Header>{label}</Header>
      <TextField
        type='datetime-local'
        fullWidth
        disabled={disableField}
        value={timestampToHTML5Input(value)}
        className={classes.textField}
        onChange={(e) => onChange(HTML5InputToTimestamp(e.target.value))}
      />
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>
  )
}

DateControl.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  label: PropTypes.string.isRequired,
  hint: PropTypes.string,
  defaultValue: PropTypes.string,
  allowEdit: PropTypes.bool,
  permaEdit: PropTypes.bool,
  onChange: PropTypes.func.isRequired
}

export default withStyles(style)(DateControl)
