import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import { IconButton } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    zIndex: 10,
    top: theme.spacing.unit,
    right: theme.spacing.unit,
    position: 'absolute'
  },
  iconButton: {
    // background: theme.palette.primary.dark,
    // color: theme.palette.primary.light,
    margin: theme.spacing.unit / 2,
    padding: theme.spacing.unit / 2,
    border: '2px solid',
    borderRadius: theme.spacing.unit,
    fontSize: 20
  },
  active: {
    background: theme.palette.secondary.dark,
    color: 'white'
  },
  icon: {

  }
})

const StyleButtons = ({ classes, currStyle, mapStyles, setCurrStyle }) => (
  <div className={classes.root}>
    {
      mapStyles.map(({ Icon, ActiveIcon }, idx) => {
        return <IconButton key={`stylebutton${idx}`}
          className={classNames(classes.iconButton, idx === currStyle && classes.active)}
          onClick={() => setCurrStyle(idx)}>
          {idx === currStyle
            ? <ActiveIcon className={classes.icon} />
            : <Icon className={classes.activeIcon} />
          }
        </IconButton>
      })
    }

  </div>
)

StyleButtons.propTypes = {
  classes: PropTypes.object.isRequired,
  currStyle: PropTypes.number,
  mapStyles: PropTypes.array,
  setCurrStyle: PropTypes.func
}

export default withStyles(style)(StyleButtons)
